import React from 'react';
import ReactPlayer from 'react-player';

function BankVideo(props) {
    return (
        <div className="card">
            <div className="card-body p-3">
                <ReactPlayer
                    url={props.urlVideo}
                    width="100%"
                    height="500px"
                />
            </div>
        </div>
    );
}

export default BankVideo;