import React from 'react';

function HomeCardMoney(props) {
    return (
        <div className="card  wallet blue">
            <div className="boxs">
                <span className="box one" />
                <span className="box two" />
                <span className="box three" />
                <span className="box four" />
            </div>
            <div className="card-header border-0">
                <div className="wallet-info">
                    <span className="font-w400 d-block text-white">{props.title}</span>
                    <h4 className="fs-24 font-w600 mb-0 d-inline-flex me-2">{props.value} đ</h4>
                </div>
                <div className="wallet-icon">
                    <svg
                        width={62}
                        height={39}
                        viewBox="0 0 62 39"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
                    >
                        <circle
                            cx="42.7722"
                            cy="19.2278"
                            r="19.2278"
                            fill="white"
                            fillOpacity="0.2"
                        />
                        <circle
                            cx="19.2278"
                            cy="19.2278"
                            r="19.2278"
                            fill="white"
                            fillOpacity="0.2"
                        />
                    </svg>
                </div>
            </div>
        </div>
    );
}

export default HomeCardMoney;