import React from 'react';

function OwnerBankItem(props) {
    const bank = props.bank;
    return (
        <div className="card overflow-hidden">
            <div className="card-body">
                <div className="text-center">
                    <div className="profile-photo">
                    <img
                            src={bank.image}
                            style={{
                                width: '200px',
                                height: '100px',
                                objectFit: 'cover',
                                borderRadius: '8px',
                            }}
                            className="img-fluid"
                            alt=""
                        />
                    </div>
                    <p className="mt-2 mb-1 text-white">{bank.name}</p>
                </div>
            </div>
            {/* <div className="card-footer pt-0 pb-0 text-center">
                <div className="row">
                    <div className="col-6 pt-2 pb-2 border-start">
                        <div className='mb-2 text-white'>Ngân Hàng</div>
                        {bank.status === 1 ? (<label className="badge badge-xl light badge-success">Đang bật</label>) : (<label className="badge badge-xl light badge-danger">Đang tắt</label>)}
                    </div>
                    <div className="col-6 pt-2 pb-2 border-end">
                        <div className='mb-2 text-white'>Thiết bị</div>
                        {bank.status === 1 ? (<label className="badge badge-xl light badge-success">Đang bật</label>) : (<label className="badge badge-xl light badge-danger">Đang tắt</label>)}
                    </div>
                </div>
            </div> */}
        </div>
    );
}

export default OwnerBankItem;